import React from 'react'
import { graphql } from 'gatsby'
import ArchiveTemplate from './archive_template'

export const query = graphql`
    query ($seriesTitle: String!){
        allMdx(filter: { frontmatter: { series: { title: { eq: $seriesTitle }}}}){
            nodes {
                ...postFields
            }
        }
    }
`
export default function SeriesArchiveTemplate( {data, pageContext} ){
    return (<ArchiveTemplate nodes={data.allMdx.nodes}/>)
}